import React from "react";
import s from './pokemonCards.module.css';
import PokemonCard from "../PokemonCard/PokemonCard";

export default class PokemonCards extends React.Component {
  render() {
    return (
      <div className={s.container}>
        {
          this.props.pokemons.length ? this.props.pokemons.map(pokemon => <PokemonCard {...pokemon} />) : <h3>No pokemons to list.</h3>
        }
      </div>
    );
  }
}
