import React from "react";
import Types from "../../Dumbs/Types/Types";
import s from './apiRegsOrderFilter.module.css';

export default class ApiRegsOrderFilter extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      api: true,
      own: true,
      apiRegs: null,
      orderBy: '',
      order: 'A'
    }
  }

  handleRegs = event => { // name: [apiRegs || api || own] // Cuando cambia, hace el Get a mi api para traer los pokemons, eso le impacta a Home. Al renderizarse home, vuelve a renderizarse tambien esto? volviendo apiregs a 40 y todo el estado local al inicial???
    this.setState(old => ({...old, [event.target.name]: (event.target.name === 'apiRegs' ? - -event.target.value : !old[event.target.name])}), () => console.log(this.state.own, this.state.api, this.state.apiRegs));
  }

  handleOrder = event => { // name: [orderBy, order]. Al cambiar order, solo afecta a PokemonsCards
    this.setState(old => ({...old, [event.target.name]: event.target.value}), () => console.log(this.state.orderBy, this.state.order));
  }

  render() {
    return <div className={s.rightBar}>
      
      <fieldset>
        <legend>Regs includes:</legend>
        <table>
          <tbody>
            <tr>
              <td>
                <input type="checkbox" id="own" name="own" checked={this.state.own} onChange={this.handleRegs} />
                <label htmlFor="own">Own DB</label>
              </td>
              <td>
                <input type="checkbox" id="api" name="api" checked={this.state.api} onChange={this.handleRegs} />
                <label htmlFor="api">Api</label>
              </td>
            </tr>
            <tr style={!this.state.api ? {display: 'none'} : {display: 'table-row'}}>
              <td colSpan={2}>
                <h6 style={{display: 'inline-block', margin: '10px'}}>Api Pokemons:</h6>
                <input type="number" defaultValue={this.state.apiRegs} style={{width: 70, marginRight: '5px'}} name="apiRegs" onChange={this.handleRegs}/> 
              </td>
            </tr>
          </tbody>
        </table>
      </fieldset>

      <fieldset>
        <legend>Order:</legend>
        <table>
          <tbody>
            <tr>
              <td colSpan={2}>
                <h6 style={{display: 'inline-block', margin: '10px'}}>Order by:</h6>
                <select name="orderBy" value={this.state.orderBy} onChange={this.handleOrder}>
                  <option value="">None</option>
                  <option value="name">Name</option>
                  <option value="weight">Weight</option>
                  <option value="attack">Attack</option>
                </select>
              </td>
            </tr>
            <tr style={this.state.orderBy === '' ? {display: 'none'} : {display: 'table-row'}}>
              <td>
                <input type="radio" value="A" id="asce" name="order" checked={this.state.order === 'A'} onChange={this.handleOrder} />
                <label htmlFor="asce">Ascending</label>
              </td>
              <td>
                <input type="radio" value="D" id="desc" name="order" checked={this.state.order === 'D'} onChange={this.handleOrder} />
                <label htmlFor="desc">Descending</label>
              </td>
            </tr>
          </tbody>
        </table>
      </fieldset>

      <fieldset>
        <legend>Type Filter:</legend>
        <Types />
      </fieldset>
    </div>;
  }
}

