//import * as aT from "../actions";

const initialState = {
  pokemons: [],
//  pokemonDetail: null, En el caso de que traiga el detalle del pokemon al estado global
  types: null,
  typesFilter: [],
  order: null,
  page: 1,
  ipp: 12,
  apiRegs: 40
};

const rootReducer = (state = initialState, {type, payload}) => {
  switch (type) {
    case 'SET_TYPES_FILTER': return {...state, typesFilter: payload};
    case 'SET_ORDER': return {...state, order: payload};
    case 'SET_PAGE': console.log({...state, page: payload}); return {...state, page: payload};
    case 'SET_IPP': return {...state, ipp: payload};
    case 'SET_API_REGS': return {...state, apiRegs: payload};
    case 'GET_POKEMONS': return {...state, pokemons: payload};
    case 'GET_TYPES': return {...state, types: payload};
//    case 'GET_POKEMON_DETAIL': return {...state, pokemonDetail: payload}; // En el caso de que traiga el detalle del pokemon al estado global
    case 'CREATE_POKEMON': return {...state, pokemons: [...state.pokemons, payload]};
    default: return {...state};
  }
};

export default rootReducer;
