import React from "react";
import s from './types.module.css';

const hardTypes = ['bug','dark','dragon','electric','fairy','fighting','fire','flying','ghost','grass','ground','ice','normal','poison','psychic','rock','shadow','steel','water','unknown'];

export default class Types extends React.Component {
  constructor(props){
    super(props);
    this.state = {types: props.types || hardTypes};
  }

  handleClick = (event) => {
    this.setState(old => ({types: (old.types.includes(event.target.name) ? old.types.filter(el => el !== event.target.name) : [...old.types, event.target.name])}));
  }

  render() {
//    console.log(this.state);
    return (
        <div className={s.container}>
          {hardTypes.map(el => (<img key={el} className={this.state.types.includes(el) ? `${s.icon} ${s.iconActive}` : s.icon} src={`./images/types/${el}.gif`} alt={el} title={el.toUpperCase()} name={el} onClick={this.handleClick} />))}
        </div>
    );
  }
}
