import React from "react";
//import s from './home.module.css';
import PokemonCards from "../../Dumbs/PokemonCards/PokemonCards";
import ApiRegsOrderFilter from "../ApiRegsOrderFilter/ApiRegsOrderFilter";
import Paging from "../../Smarts/Paging/Paging";
import {connect} from 'react-redux';

class Home extends React.Component {
  render() {
    console.log(this.props);

    return (
      <div>
        <ApiRegsOrderFilter /*apiRegs = {this.state.apiRegs}*/ />
        <Paging pokemonsCount={this.props.pokemons.length} />
        <PokemonCards pokemons={this.props.pokemons} />
      </div>
    );
  }
}

export default connect(({pokemons, typesFilter}) => ({pokemons, typesFilter}))(Home);