import React from "react";
import {connect} from "react-redux";
import s from './paging.module.css';
import {setPage, setIpp} from '../../../redux/actions';

class Paging extends React.Component {
    handlePageChange = (newPage) => {
        (newPage > 0) && ((newPage * this.props.ipp) <= this.props.pokemonsCount)
            && this.props.setPage(newPage);
    }

    render() {
        return (
            <form className={s.paging}>
                <div onClick={() => this.handlePageChange(this.props.page - 1)} style={{display: 'inline-block', marginRight: '40px', cursor: 'pointer'}}><h3>Prev</h3></div>
                <h6 style={{display: 'inline-block', marginRight: '30px'}}>({this.props.pokemonsCount} Pokemons)</h6>
                <h5 style={{display: 'inline-block', marginRight: '5px'}}>Page:</h5>
                <input type="number" value={this.props.page} style={{width: 35, marginRight: '5px'}} name="page" id="page" onChange={event => this.handlePageChange(event.target.value)}/> 
                <h5 style={{display: 'inline-block', marginRight: '30px'}}>/ {3}</h5>
                <h5 style={{display: 'inline-block', marginRight: '5px'}}>IPP:</h5>
                <input type="number" value={this.props.ipp} style={{width: 35, marginRight: '5px'}} name="ipp" id="ipp" onChange={event => (event.target.value > 0) && this.props.setIpp(event.target.value)} /> 
                <div onClick={() => this.handlePageChange(this.props.page + 1)} style={{display: 'inline-block', paddingLeft: '40px', cursor: 'pointer'}}><h3>Next</h3></div>
            </form>
        );
    }
}

export default connect(({page, ipp, apiRegs}) => ({page, ipp, apiRegs}), {setPage, setIpp})(Paging);